$tabletWidth: 1400px
$padWidth: 1100px
$phoneWidth: 415px

@mixin tablet
  @media screen and (max-width: $tabletWidth) 
    @content
@mixin pad
  @media screen and (max-width: $padWidth) 
    @content
@mixin phone 
  @media screen and (max-width: $phoneWidth) 
    @content

// ant-design覆寫
body
  font-size: 18px
  @include phone
    font-size: 16px
#root
  height: 100%
  .ant-layout
    min-height: 100%
a
  color: rgba(0, 0, 0, 0.45)
h1
  font-weight: bold
pre
  white-space: pre-wrap
  white-space: -moz-pre-wrap
  white-space: -pre-wrap
  white-space: -o-pre-wrap
  word-wrap: break-word
.ant-form
  font-size: 1em
.ant-menu
  font-size: 1em
  .ant-menu-item
    font-size: 1em
.ant-form-item
  font-size: 1em
.ant-form-item-label
  > label
    font-size: 1em
.ant-layout-footer
  font-size: 0.8em
  @include phone
    padding: 16px 50px
// breadcrumb
.breadcrumb 
  list-style: none
  &__item
      display: inline-block
      color: rgba(0, 0, 0, 0.65)
      &--active
        &::after 
          display: none
      &::after 
        content: "/"
        display: inline-block
        padding: 0 0.3em
        color: rgba(0, 0, 0, 0.45)
      a
        display: inline-block
// header
.header
  background: #fff
  padding: 0
// content
.content
  margin: 24px 16px 0
  &__item
    padding: 24px
    min-height: 420px
    background: #fff
// sider
.sider
  &__logo
    height: 32px
    background: rgba(255, 255, 255, 0.2)
    margin: 16px
// singleContent
.singleContent
  display: -webkit-flex
  display: flex
  -webkit-align-items: center
  align-items: center
  -webkit-justify-content: center
  justify-content: center
  overflow-x: hidden
  flex-direction: column
  &__title
    font-weight: bold
  &__item
    padding: 24px
    background: #fff
    box-shadow: 1px 4px 12px 4px rgba(51,51,102,.1)
  .ant-form-item-label 
    > label
      font-size: 16px