$tabletWidth: 1400px
$padWidth: 1100px
$phoneWidth: 415px

@mixin tablet
  @media screen and (max-width: $tabletWidth) 
    @content
@mixin pad
  @media screen and (max-width: $padWidth) 
    @content
@mixin phone 
  @media screen and (max-width: $phoneWidth) 
    @content

.appPage
  display: -webkit-flex
  display: flex
  -webkit-align-items: center
  align-items: center
  -webkit-justify-content: center
  justify-content: center
  overflow-x: hidden
  flex-direction: column
  padding: 50px
  @include phone
    padding: 0
  .ant-layout
    width: 40%
    background: #fff
    box-shadow: 1px 4px 12px 4px rgba(51,51,102,.1)
    @include pad
      width: 70%
    @include phone
      width: 100%
  .content
    margin: 24px 16px 30px 16px
  .content__item
    padding: 0 24px